import { h, Component } from 'preact';
import { login } from '../helpers/auth';
import { ContentContainer, StyledInput, StyledButton }  from '../styles/base';
import { Line }  from '../styles/profile';

export default class Login extends Component {
  constructor() {
    super();

    this.state = {
      userName: '',
      password: '',
      showError: false
    }
  }

  signIn() {
    const { userName, password } = this.state;

    if (!userName || !password) return;

    this.setState({ showError: false });
    login(userName, password).then((success) => {
      this.setState({ showError: !success });
    });
  }

  render({ }, { userName, password, showError }) {

    return (
      <ContentContainer centered>
        <h3>Bejelentkezs</h3>
        <Line>
          <label>Felhasznlnv</label>
          <StyledInput
            placeholder="Felhasznlnv"
            type="text"
            value={userName}
            onChange={(e) => { this.setState({ userName: e.target.value }); }}
            simple
          />
        </Line>
        <Line>
          <label>Jelsz</label>
          <StyledInput
            placeholder="Jelsz"
            type="password"
            value={password}
            onChange={(e) => { this.setState({ password: e.target.value }); }}
            simple
          />
        </Line>
        <br />
        <StyledButton
          onClick={() => { this.signIn(); }}
        >Bejelentkezs</StyledButton>

        {showError ? <p>A felhasznlnv vagy jelsz nem rvnyes!</p> : null}
      </ContentContainer>
    );
  }
};



